@import url(https://use.typekit.net/hji6emp.css);
@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300&display=swap);

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Merriweather", "sans-serif";
  color: #5c5c5c;
  font-size: 14px;
}

p {
  opacity: 0.7;
  line-height: 1.5rem;
  font-size: 1rem;
}

.app-container {
  margin: 0;
  padding-right: 17px;
  padding-left: 17px;
  min-height: 90vh;
  background-color: #fafafa;
}

.main-content {
  width: 100%;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: #9e9e9e;
}

.app__mobileMenuLink {
  font-family: proxima-nova, sans-serif;
  color: #585858be;
  border-bottom: 2px solid #cfcfcfd8;
  display: block;
  width: 100%;
  padding: 10px;
  margin-bottom: 1rem;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  letter-spacing: 1.5px;
  font-size: 16px;
}

.headerNavReverse {
  display: flex;
  flex-direction: column;
}

.pages__description {
  text-align: center;
  font-size: 14px;
  line-height: 1.9em;
  font-weight: 300;
  overflow-wrap: break-word;
  padding: 17px 0;
}

@media screen and (min-width: 510px) {
  .app-container {
    margin: 0;
    padding: 0 17px;
  }
  .app__mobileMenuLink {
    display: none;
  }
  .headerNavReverse {
    flex-direction: column-reverse;
  }
}

@media screen and (min-width: 768px) {
  .app-container {
    padding: 50px 50px 0 50px;
    margin: 50px auto;
  }
}
@media screen and (min-width: 900px) {
  .app-container {
    padding: 100px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.1);
    max-width: 1100px;
  }

  .main-content {
    width: 70%;
    margin: 82px auto;
  }
}

.navbar2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
  font-weight: 400;
  top: 0;
  visibility: visible;
  overflow: visible;
  transition: all 0.1s ease;
  max-height: 100%;
  border-bottom: 2px solid #cfcfcfd8;
}

a {
  font-family: proxima-nova, sans-serif;
}

.navbar2__list {
  width: 100%;
  margin: 0.75rem 0;
}

.navbar2__list a {
  cursor: pointer;
}

.navbar2__listItem {
  padding: 0.5rem;
  font-size: 16px;
  letter-spacing: 2px;
}

.navbar2__listItem-selected {
  color: black;
}

.navbar2--hide {
  visibility: hidden;
  overflow: hidden;
  max-height: 0;
}

.navbar__link--active {
  color: black;
  font-size: 1.05rem;
}
@media screen and (min-width: 510px) {
  .navbar2 {
    border: 0;
  }
  .navbar2__list {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.banner__wrapper {
  background: black;
  display: flex;
  justify-content: center;
  width: 100%;
  border-bottom: black 1px solid;
  padding: 1rem;
  text-align: center;
  position: static;
  top: 0;
}
.banner__p {
  color: white;
  font-family: proxima-nova, sans-serif;
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* padding: 0 0 4rem 0; */
}

.header__logo {
  border: transparent;
  border-radius: 50%;
  width: 70vw;
  object-fit: contain;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  max-width: 250px;
}

.header__title {
  padding: 3rem 0;
  font-family: proxima-nova, sans-serif;
  color: #9e9e9e;
  letter-spacing: 2.5px;
  font-weight: 400;
  line-height: 40px;
}

/* @media screen and (min-width: 768px) {

} */

.qna__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  margin-bottom: 30px;
  text-align: center;
  padding: 0 3rem;
  line-height: 1.5rem;
}

.title h2 {
  font-weight: bold;
  font-family: Merriweather, sans-serif;
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  font-style: italic;
  /* opacity: 0.7; */
  line-height: 4rem;
}

img {
  width: 100%;
  object-fit: contain;
}

.project__heading {
  font-family: proxima-nova, sans-serif;
  text-align: center;
  margin-bottom: 17px;
}
.project__headingDesc {
  font-family: Merriweather;
  font-weight: 300;
  font-style: normal;
  line-height: 1.9em;
  font-size: 14px;
  text-transform: none;
  text-decoration: none;
  margin: 0 0 1.9em;
}
.project__headingTitle {
  font-size: 1.3rem;
  text-transform: uppercase;
  font-family: proxima-nova, sans-serif;
  font-weight: bold;
  line-height: 1.6em;
  letter-spacing: 0.79px;
  margin-bottom: 0.2rem;
}
.project__imageWrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.project__img {
  /* display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden; */
  width: 50%;
}
.project__img img {
  object-fit: cover;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .project__imageWrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    grid-gap: 17px;
  }
  .project__img {
    width: 30%;
    height: 100%;
  }
}

.artcard__wrapper {
  display: flex;
  margin-bottom: 17px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}
.artcard__wrapper a {
  height: 100%;
}

.artcard-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
}

.artcard-img:hover {
  transform: scale(0.99);
}

.caption__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #ffffff;
  padding: 10%;
}

.caption-title {
  font-weight: 300;
  margin-bottom: 0.3rem;
  font-style: normal;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  font-family: proxima-nova, sans-serif;
}

.caption {
  font-weight: 400;
  font-style: normal;
  letter-spacing: 0em;
  text-transform: none;
  opacity: 0.5;
  margin: 0 auto;
}

@media screen and (min-width: 500px) {
  .artcard__wrapper {
    flex-direction: row;
    justify-content: space-between;
    box-shadow: none;
    position: relative;
    margin: 17px;
  }
  .artcard-img {
    width: 49%;
    /* box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2); */
  }
  .caption__wrapper {
    width: 49%;
    padding: 5%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 51%;
  }
}

@media screen and (min-width: 768px) {
  .artcard__wrapper {
    height: 210px;
  }
}
@media screen and (min-width: 1024px) {
